import counter from '../../../../common/counter.vue'
import common from '../../../../common/common.js'
var serverUrl = common.serverUrl
export default {
	components: {
		counter
	},
	data() {
		return {
			footerbottom: 0,
			theIndex: null, // 控制滑动效果 当前滑动下标
			oldIndex: null, // 上一个左滑下标
			goodsList: [],
			selectedList:[],
			isAllSelected: false,
			sumPrice: '0.00',
			userId:"",
			userInfo:{},
			token:'',
			type:'',
			carts:{}
		}
	},
	methods: {
		add(data){
			data.number++;
			if(data.number===1){data.number=1}
			uni.request({
				url:serverUrl+'/carts/'+data.goods_id+'?userId='+this.userId+'&count='+data.number,
				method:'PUT',
				success: (res) => {
					if(res.data.code===0){
						debugger
						this.goodsList=res.data.data.cartDTOList
					}
				}
			})
		},
		sub(data){
			data.number--;
			if(data.number===1){data.number=1}
			uni.request({
				url:serverUrl+'/carts/'+data.goods_id+'?userId='+this.userId+'&count='+data.number,
				method:'PUT',
				success: (res) => {
					if(res.data.code===0){
						this.goodsList=res.data.data.cartDTOList
					}
				}
			})
		},
		handleCheckbox(item) { // 单选
			// console.log(item);
			item.selected = !item.selected;
			
			// 条件: 数组中是否包含该元素
			let isExist = this.selectedList.indexOf(item);
			if(isExist > -1){
				this.selectedList.splice(isExist,1);
			}else {
				// push 数组里
				this.selectedList.push(item);
			}
			
			// 全选状态
			if(this.selectedList.length == this.goodsList.length){
				this.isAllSelected = true;
			}else {
				this.isAllSelected = false;
			}
			
			this.sum();
		},
		handleSelectedAll(){ // 全选
			this.isAllSelected = !this.isAllSelected;
			
			// 数据处理
			let arr = [];
			this.goodsList.forEach((item,i) => {
				item.selected = this.isAllSelected;
				arr.push(item);
			})
			
		    this.selectedList =	this.isAllSelected ? arr : [];
			
			// 调用合计
			this.sum();
		},
		sum(){// 合计
			this.sumPrice = 0;
			this.goodsList.forEach((item,i) => {
				if(item.selected){
					this.sumPrice = this.sumPrice + (item.number * item.price)
				}
			})
			this.sumPrice  = this.sumPrice.toFixed(2);
		},
		handleSingleDelete(item){
			// 更新storage
			// uni.getStorage({
			// 	key:"goodsList",
			// 	success: (res) => {
			// 		res.data.splice(res.data.indexOf(item),1);
			// 		uni.setStorageSync("goodsList",res.data);
			// 	}
			// })
			
			// // 更新数组
			// this.goodsList.splice(this.goodsList.indexOf(item),1);
			// this.selectedList.splice(this.selectedList.indexOf(item),1);
			
			// this.oldIndex = null;
			// this.theIndex = null;
			
			// this.sum();
		},
		handleMulDelete(){
			// 循环删除所有选中的商品
			while(this.selectedList.length > 0){
				this.handleSingleDelete(this.selectedList[0]);
			}
			
			// 初始化数据
			this.selectedList = [];
			this.isAllSelected = false;
			this.sum();
		},
		handleGoodsInfo(item) {
			uni.navigateTo({
				url: "../../goods/goods?goodsInfo=" + JSON.stringify(item)
			})
		},
		handleTouchStart(index, event) {
			// 多点触控 不触发
			if (event.touches.length > 1) {
				return;
			}

			this.oldIndex = this.theIndex;
			this.theIndex = null;

			// 初始化坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		handleTouchMove(index, event) {
			// 多点触控 不触发
			if (event.touches.length > 1) {
				return;
			}

			// 移动位置
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			// 滑动位置小 不触发
			if (Math.abs(moveX) < 5) {
				return;
			}

			// 竖向滑动 不触发
			if (Math.abs(moveY) > Math.abs(moveX)) {
				return;
			}

			// 左滑
			if (moveX < 0) {
				this.theIndex = index;
			}
		},
		handleTouchEnd(index, event) {
			
		},
		handleConfirm(){ // 结算
			if(this.selectedList.length < 1){
				uni.showToast({
					title:"请选择结算的商品",
					icon:"none"
				})
				return;
			}
			
			// 本地存储
			uni.setStorage({
				key:"confirmList",
				data: this.selectedList,
				success: () => {
					uni.navigateTo({
						url:"../../order/confirm"
					})
				}
			})
		}
	},
	onShow() {
		// uni.getStorage({
		// 	key: "goodsList",
		// 	success: (res => {
		// 		// 将所有商品的选中状态都设置false
		// 		for (let i = 0; i < res.data.length; i++) {
		// 			res.data[i].selected = false;
		// 		}
		// 		this.goodsList = res.data;
		// 		console.log(this.goodsList)
				
		// 		// 属性数据的初始化
		// 		this.selectedList = [];
		// 		this.isAllSelected = false;
		// 		this.sumPrice = '0.00';
		// 	})
		// })
		var userInfo = uni.getStorageSync("globalUser")
		if(userInfo!=null&&userInfo!=""&&userInfo!=undefined){
			this.userInfo=userInfo
			this.token="Bearer "+this.userInfo.token
			this.type=this.userInfo.tokenType
			this.userId=this.userInfo.userId
		}
		
		uni.request({
			url:serverUrl+'/carts/'+this.userId,
			method:'GET',
			success: (res) => {
				if(res.data.code===0){
					this.goodsList=res.data.data.cartDTOList
					
				}
			}
		})
	},
	onLoad() {
		// 兼容h5下的底部菜单



	}
}
